/* TableComponent.css */

div {
    text-align: center;
    margin: 20px;
  }
  
  h2 {
    color: #333;
  }
  
  table {
    border-collapse: collapse;
    width: 100%;
    margin-top: 20px;
  }
  
  th, td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
  }
  
  th {
    background-color: #f2f2f2;
  }
  
  p {
    margin-top: 10px;
    font-size: 1.2em;
    color: #555;
  }
  
  /* Loading spinner */
  @keyframes spinner {
    to {transform: rotate(360deg);}
  }
  
  p.loading::after {
    content: ' ';
    display: inline-block;
    width: 16px;
    height: 16px;
    border: 2px solid #333;
    border-radius: 50%;
    border-top: 2px solid #f2f2f2;
    animation: spinner 0.8s linear infinite;
    vertical-align: middle;
    margin-left: 8px;
  }
  